.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #0081a8;
  min-height: 2vm;
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-products {
  background-color: rgba(0, 189, 189, 0.452);
  margin: auto;
  padding: 1em;
}

.Products-item {
  background-color: rgba(255, 255, 255, 0.37);
  width: 30%;
  margin: 10px auto;
  border: 1px solid black;
  border-radius: 2em;
  padding-bottom: 1em;
}

.Products-item > img {
  margin-top: 1em;
  width: 80%;
  height: 50%;
  border: 2px solid rgba(0, 0, 0, 0.377);
  border-radius: 2em;
}

.Shopping-list {
  position: fixed;
  right: 0;
  bottom:5%;
  width: 35%;

  border: 1px solid rgba(0, 0, 0, 0.425);
  border-radius: 30px;

  background-color: rgba(255, 255, 255, 0.705);

  padding-top: 1em;
  padding-bottom: 1em;
  max-width: 400px;
  
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.Shopping-list-item{
  background-color: aliceblue;
  width: 80%;
  margin: 1em auto;
  display: flex;
  flex-direction: row;
  flex-wrap:nowrap;
  justify-content: center;
  padding-left: 4%;
  border: 1px solid rgba(0, 0, 0, 0.425);
  border-radius: 20px;
}

.Shopping-list-item-item{
  margin: auto;

}

.Shopping-list-item-item > button {
  color: black;
  background-color: red;
}

.Shopping-list-item-item > button:hover{
  background-color: #c70000;
}


.Shopping-continue{
  width: 40%;
  margin: auto;
  background-color: rgba(240, 248, 255, 0.767);
  border: solid black 1px;
  border-radius: 1em;
  padding: 10px;
}

.Products-item > button{
  background-color: rgba(240, 248, 255, 0.767);
  border: solid black 1px;
  border-radius: 1em;
  padding: 10px;
}

.Products-item > button:hover{
  background-color: #1100ff81;
}

.Contact-form {
  background-color: rgba(0, 189, 189, 0.452);
  border: 2px solid black;
  border-radius: 3em;
  width: 40%;
  padding-bottom: 2%;
  margin: 5em auto;
}

input[type="submit"]{
  background-color: rgba(240, 248, 255, 0.767);
  border: solid black 1px;
  border-radius: 1em;
  padding: 10px;
}

input[type="submit"]:hover{
  background-color: #0081a8;
}

input[type="text"], input[type="email"]{
  background-color: #ffffff70;
  padding: 1em;
  margin: 4px auto;
  border: 1px solid black;
  border-radius: 2em;
  text-align: center;
}

.App-conclusion {
  background-color: rgba(0, 189, 189, 0.452);
  width: 60%;
  margin: 1em auto;
  border: 2px solid rgba(0, 0, 0, 0.479);
  border-radius: 4em;
  padding-top: 1em;
  padding-bottom: 3em;
}

.App-conclusion > button{
  background-color: rgba(240, 248, 255, 0.767);
  border: solid black 1px;
  border-radius: 1em;
  padding: 10px;
}

.App-conclusion > button:hover{
  background-color: #1100ff81;
}




.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
